import axios from 'axios';

axios.defaults.headers.common['Content-Type'] = 'x-www-form-urlencoded';
const apiPath = "/api/v2/";

const AxiosService = {
    sendLogin: async function (origin, info) {
        try {
            const params = new URLSearchParams();
            params.append('username', info.username);
            params.append('password', info.password);

            const response = await axios.post(`${origin}${apiPath}auth/login`, params);
            console.log(response);
        } catch (error) {
            console.error(error);
        }
    }
}

export default AxiosService;